@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed);

.loader {
  position: fixed;
  margin: auto;
  width: 350px;
  color: white;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 250%;
  background: linear-gradient(180deg, #222 0, #444 100%);
  box-shadow: inset 0 5px 20px black;
  text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
  justify-self: center;
  align-self: center;
  text-align: center;
}

.loader:after {
  content: '';
  display: table;
  clear: both;
}
.loader {
  span {
    float: left;
    height: 100px;
    line-height: 120px;
    width: 50px;
  }
}

.loader > span {
  border-left: 1px solid #444;
  border-right: 1px solid #222;
  text-align: center;
}

.covers {
  position: absolute;
  height: 100%;
  width: 100%;
}

.covers span {
  background: linear-gradient(180deg, rgb(116, 116, 116) 0, rgb(121, 121, 121) 100%);
  animation: up 2s infinite;
}

@keyframes up {
  0% {
    margin-bottom: 0;
  }
  16% {
    margin-bottom: 100%;
    height: 20px;
  }
  50% {
    margin-bottom: 0;
  }
  100% {
    margin-bottom: 0;
  }
}

.covers span:nth-child(2) {
  animation-delay: 0.142857s;
}
.covers span:nth-child(3) {
  animation-delay: 0.285714s;
}
.covers span:nth-child(4) {
  animation-delay: 0.428571s;
}
.covers span:nth-child(5) {
  animation-delay: 0.571428s;
}
.covers span:nth-child(6) {
  animation-delay: 0.714285s;
}
.covers span:nth-child(7) {
  animation-delay: 0.857142s;
}
